html {
  box-sizing: border-box;
  font-family: sans-serif;
  background: black;
}
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.header {
  font-size: 2em;
  color: white;
  text-align: center;
  flex: 1 0 100%;
  height: 50px;
}

.keys {
  background: url(https://imgur.com/5ZpQsLl.jpg) bottom center;
  background-size: cover;
  display: flex;
  flex-wrap: wrap;
  /* min-height: 100vh; */
  height: 100vh;
  width: 100vw;
  max-height: 800px;
  max-width: 1000px;
  padding-bottom: 5px;
  padding-right: 5px;
}

.key {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 0 30%;
  margin-left: 5px;
  margin-top: 5px;
  transition: all 0.1s ease;
  color: white;
  background: rgba(0, 0, 0, 0.6);
  text-shadow: 0 0 2.5rem black;
}

.key:hover {
  background: rgba(68, 66, 66, 0.6);
  cursor: pointer;
}
.playing {
  transform: scale(0.8);
}

kbd {
  display: block;
  font-size: 4rem;
}

.sound {
  font-size: 2rem;
  letter-spacing: 0.1rem;
  color: #f85858;
}
